#root{
  width: 100%;
}

html{
  width: 100%;
}
body {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main{
    flex:1 0 auto;
    background-color: grey;
    display: flex;
    

}


.contentdisp{
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
}
.header{
    flex: 0 0 auto;
    background-color: #4E4E4E;
    display:flex;
    align-items: center;
    flex-direction: row;
    padding: 50px 0px;
   
    
}


@media only screen and (max-width: 768px) {
    .header{
        flex-direction: column;
        padding-top: 10px;
    }
  }


.media-art{
    flex: 0 0 50px;
    padding-left: 40px;
    
}


@media only screen and (max-width: 768px) {
   .media-art > img{
       width: 150px;
    }

    .media-art{
        padding-left: 0px;
    }
  }


.nowplaying-container{
    display: flex;
    flex-direction: column;
    /* align-self: baseline; */
    color:whitesmoke;
    margin-left: 50px;
    font-family: 'Cinzel', serif;
    font-weight: normal;
    height: 75%;
}
 
.nowplaying{
    margin-bottom: auto;
}


p{
    margin: 0px;
    font-size:1em;
}

.sm-nav{
    display: flex;
    align-items: center;
}

.sm-nav > div{
 
    font-size: 12px;
    /* display: inline; */
    flex:0 1 auto;
    /* max-width: 70px;; */
    
}

.bg-color-change > p{
    padding: 10px;
}


.bg-color-change :hover{
    background-color:rgb(175, 175, 175);
    cursor: pointer;
}

.nowplaying > h1{
    margin-top: 0%;
}

.bg-color-change> p{
    margin-bottom: 0px;
}

@media only screen and (max-width: 768px) {
    .nowplaying > h1{
        font-size: 18px;
        line-height: 30px;
        padding: 10px;
    }
    .nowplaying > p {
        display: none;
    }

    .nowplaying-container{
        margin: 0px;
    }

    .nowplaying{
        margin: 0px;
    }

    .sm-nav{
        margin: 0 auto;
    }

    .sm-nav > div {
        max-width: 100px;
    }
  }


.playlist{
    flex: 1 0 auto;
    background-color: #FFFFFF;
    padding-top: 20px;
}


.playlist-container{
    display: flex;
    flex-direction: row;
    padding: 10px 10px;
    font-size: 12px;
    font-weight: normal;
    width: 95%;
    margin: 0 auto;
   

}



.setting-equal-width{
    flex: 0 0 25%;
    padding: 0 10px;
}

.ps-type{
    flex: 0 1 70px; ;
    text-align: center;
    padding: 1px;
    font-size: 8px;
    border: 1px solid black;
    height: 5%;
}
.grey{
    background-color:#F2F2F2;
    border : 1px solid rgb(201, 201, 201); 
}

.white{
    background-color: white;
}

@media screen and (max-width: 769px) {
    div.setting-equal-width:nth-child(2){
        display: none;
    }
    /* div.setting-equal-width:nth-child(3){
        text-align: center;
    } */

    div.ps-type{
        display: none;
    }

    .setting-equal-width{
        flex: 0 1 50%;
        padding: 0 10px;
    }
    .playlist-container{
        justify-content: flex-end;
    }
    }
.sidebar{
    flex: 0 0 40px;
    background-color: #F2F2F2;
    display: flex;
    flex-direction: column;
    align-items: center;
    

}
 
.sidebar > i{
    flex:0 0 60px;
    padding-top:10px;
    padding-bottom: 10px; 

}

.sidebar > i:first-child{
    margin-top: 100%;
}

.sidebar > i:hover{
    cursor: pointer;
    color: rgb(173, 173, 173)
}

@media screen and (max-width: 769px) {
    .sidebar{
        display: none;
    }
    }
.footer{
    background-color: #5073A7;
    flex: 0 0 75px;
    width: auto;
    display: flex;
    align-items: center;
} 
.media-art-container{
   
  
    justify-content: center;
    flex:0 0 75%;

}

.left-container{
    display: flex;
    align-items: center;
 

}

.img-container{
    height: 82px;
}

.img{
    padding: 1px 10px;
    width: 60px;
    height: 80px;
}

.track-info-container{
    
    color:white;
    font-weight: normal;
    font-size: 15px;
    
}

.track-info-container :hover{
    cursor: pointer;
    color:rgb(185, 185, 185);
}

.first-p {
    padding-bottom: 10px;
}

@media screen and (max-width: 769px) {
    .media-art-container{
        flex: 0 0 65%;
    }
}

.footer-nav{

    display: flex;
    align-items: center;
    flex: 0 0 25%;
    margin-right: 10px;
    justify-content: center;
   
}

.footer-nav > .fas {
    text-align: center;
    flex: 0 0 20%;
    padding: 40px 0;
    color: white;
    font-size: 12px;
   
}

.footer-nav > i:hover{
    background-color: rgb(48, 68, 99);
    cursor: pointer;
}

@media screen and (max-width: 769px) {
    .footer-nav > i:nth-child(n+4){
        display: none;
        
    }

    .footer-nav{
        flex: 0 0 35%;
        margin-right: 0px;
    }
}
.app-container{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  

}
